.input__element {
  width: 100%;
  min-height: 46px;
  padding: 15px;
  margin: 10px 0;
  box-sizing: border-box;
  background-color: #2F2F2F;
  border: none;
  border-radius: 8px;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #fff;
}

.input__element:focus {
  outline: none;
}